<template>
  <div class="main">
    <div class="row">
      <div class="row-1">
        <profile-base
          class="card" 
          :waiting="waiting"
          :name="baseItems.name"
          :company="baseItems.company"
          :picture="baseItems.picture"
          :sns="baseItems.sns"/>
        <profile-skill 
          class="card" 
          :waiting="waiting"
          :image="skillItems.image"
          :title="skillItems.title"
          :lang="skillItems.lang"/>
      </div>
      <div class="row-2">
        <profile-bio 
          class="card" 
          :waiting="waiting"
          :image="bioItems.image"
          :title="bioItems.title"
          :text="bioItems.text"/>
        <div class="row">
          <profile-info 
            class="row-3 card"
            :waiting="waiting"
            :image="infoItems.image"
            :title="infoItems.title"
            :info="infoItems.info"/>
          <profile-tool 
            class="row-4 card"
            :waiting="waiting"
            :image="toolItems.image"
            :title="toolItems.title"
            :tools="toolItems.tool"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import profileBase from '@/components/profile/profile-base'
import profileSkill from '@/components/profile/profile-skill'
import profileBio from '@/components/profile//profile-biography'
import profileInfo from '@/components/profile/profile-information'
import profileTool from '@/components/profile/profile-tool'

export default {
  name: 'Profile',
  components: {
    profileBase,
    profileSkill,
    profileBio,
    profileInfo,
    profileTool
  },
  data () {
    return {
      waiting: true,
      baseItems: {
        name: '',
        company: '',
        picture: '',
        sns: []
      },
      skillItems: {
        title: 'Skills',
        image: 'plus-one.svg',
        lang: []
      },
      bioItems: {
        title: 'Profile',
        image: 'profile.svg',
        text: ''
      },
      infoItems: {
        title: 'Information',
        image: 'info.svg',
        info: {
          name: '',
          date_of_birth: '',
          tel: '',
          email: '',
          address: ''
        }
      },
      toolItems: {
        title: 'Toolbox',
        image: 'wrench.svg',
        tool: []
      }
    }
  },
  created () {
    this.$http.get('https://mygumi.me:3000/profile/1').then(res => {
      let datas = res.data
      let info = datas.informations[0]
      let toolbox = datas.toolboxes[0]

      setTimeout(() => {
        this.waiting = false
        this.baseItems.name = datas.profile_name
        this.baseItems.company = datas.profile_company
        this.baseItems.picture = datas.profile_imageURL
        this.baseItems.sns = datas.socials
        this.bioItems.text = datas.profile_bio
        this.skillItems.lang = datas.skills
        this.infoItems.info.name = info.info_name
        this.infoItems.info.date_of_birth = info.info_date_of_birth
        this.infoItems.info.tel = info.info_tel
        this.infoItems.info.email = info.info_email
        this.infoItems.info.address = info.info_address
        this.toolItems.tool = toolbox.toolbox_kind.split(',')
      }, 1000)
    })
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  
</style>



